@import url(https://fonts.googleapis.com/css?family=Inter:300,400|Oswald:700&display=swap);
*{box-sizing:border-box}html,body{scroll-behavior:smooth}::-webkit-scrollbar{background:#020C17;width:10px}::-webkit-scrollbar-thumb{background:rgba(240,240,240,0.2)}body{margin:0;color:#fff;background:#020C17;font-family:Inter, system-ui;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:20px;font-weight:300}@media (max-width: 768px){body{font-size:18px}}@media (max-width: 520px){body{font-size:16px}}header{font-size:18px;height:70px;padding:20px;position:fixed;top:0;left:0;width:100%;display:flex;align-items:center;justify-content:space-between;z-index:100}header .logo{font-family:Oswald, Impact;font-weight:600;color:#dbdbdb}header .menu{display:flex;align-items:center}header .menu button{font-family:inherit;background:none;border:none;outline:none;cursor:pointer}header .menu button svg{width:30px}section{min-height:100vh;display:flex}section .background{position:absolute;z-index:-1;pointer-events:none}img{max-width:100%}h1{font-family:Oswald, Impact, system-ui;font-size:80px;font-weight:700;text-transform:uppercase;margin-bottom:.5em}@media (max-width: 600px){h1{font-size:60px}}.container{width:1200px;max-width:90%;margin:0 auto}.button{font-family:Oswald, Impact, system-ui;color:#020C17;background:#fff;padding:9px 18px;font-size:18px;border-radius:7px;text-transform:uppercase;text-decoration:none;outline:none;cursor:pointer;border:none;box-shadow:0 2px 16px -4px rgba(0,0,0,0.25)}form{margin-bottom:2em}.input-field{position:relative;margin-bottom:1.5em;border-bottom:solid 1px rgba(255,255,255,0.4)}.input-field input,.input-field label,.input-field span{transition:250ms cubic-bezier(0.32, 1.01, 0.81, 1)}.input-field span,.input-field label{position:absolute}.input-field span{left:50%;transform:translateX(-50%);bottom:-1px;height:2px;width:0;background:#fff;pointer-events:none;opacity:0}.input-field label{left:0;top:28px;font-size:18px;transform:translateY(-50%);pointer-events:none;color:#fff}.input-field label.active{top:0;font-size:13px}.input-field input,.input-field textarea{outline:none;border:none;font-family:inherit;font-size:inherit;font-weight:300;color:rgba(255,255,255,0.9);padding:16px 0;width:100%;background:transparent}.input-field input[type="number"]::-webkit-inner-spin-button,.input-field textarea[type="number"]::-webkit-inner-spin-button{display:none;-webkit-appearance:none;appearance:none}.input-field input input[type=number],.input-field textarea input[type=number]{-webkit-appearance:none;appearance:none;-moz-appearance:textfield}.input-field input:focus ~ label,.input-field textarea:focus ~ label{top:0;font-size:13px}.input-field input:focus ~ label ~ span,.input-field textarea:focus ~ label ~ span{width:100%;opacity:1}.input-field input textarea,.input-field textarea textarea{resize:none;height:90px}.input-field input textarea::-webkit-scrollbar-thumb,.input-field textarea textarea::-webkit-scrollbar-thumb{border-radius:7px}.input-field input.hobby,.input-field textarea.hobby{display:none}footer{background:rgba(0,0,0,0.3);font-size:14px;padding:14px;text-align:center;color:rgba(250,250,250,0.7)}

