@import url('https://fonts.googleapis.com/css?family=Inter:300,400|Oswald:700&display=swap');

$main: #020C17;

* {
  box-sizing: border-box;
}

html,
body {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  background: $main;
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background: rgba(#f0f0f0, .2);
}

body {
  margin: 0;
  color: #fff;
  background: $main;
  font-family: Inter, system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 20px;
  font-weight: 300;

  @media (max-width: 768px) {
    font-size: 18px;
  }

  @media (max-width: 520px) {
    font-size: 16px;
  }
}

header {
  font-size: 18px;
  height: 70px;
  padding: 20px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 100;

  .logo {
    font-family: Oswald, Impact;
    font-weight: 600;
    color: #dbdbdb;
  }

  .menu {
    display: flex;
    align-items: center;

    button {
      font-family: inherit;
      background: none;
      border: none;
      outline: none;
      cursor: pointer;

      svg {
        width: 30px;
      }
    }

    .button {
      // margin-right: 10px;
    }
  }
}

section {
  min-height: 100vh;
  display: flex;

  .background {
    position: absolute;
    z-index: -1;
    pointer-events: none;
  }
}

img {
  max-width: 100%;
}

h1 {
  font-family: Oswald, Impact, system-ui;
  font-size: 80px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: .5em;

  @media (max-width: 600px) {
    font-size: 60px;
  }
}

.container {
  width: 1200px;
  max-width: 90%;
  margin: 0 auto;
}

.button {
  font-family: Oswald, Impact, system-ui;
  color: $main;
  background: #fff;
  padding: 9px 18px;
  font-size: 18px;
  border-radius: 7px;
  text-transform: uppercase;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  border: none;
  box-shadow: 0 2px 16px -4px rgba(0, 0, 0, .25);

}

form {
  margin-bottom: 2em;
}

.input-field {
  position: relative;
  margin-bottom: 1.5em;
  border-bottom: solid 1px rgba(#fff, 0.4);

  input,
  label,
  span {
    transition: 250ms cubic-bezier(0.32, 1.01, 0.81, 1);
  }

  span,
  label {
    position: absolute;
  }

  span {
    left: 50%;
    transform: translateX(-50%);
    bottom: -1px;
    height: 2px;
    width: 0;
    background: #fff;
    pointer-events: none;
    opacity: 0;
  }

  label {
    left: 0;
    top: 28px;
    font-size: 18px;
    transform: translateY(-50%);
    pointer-events: none;
    color: #fff;

    &.active {
      top: 0;
      font-size: 13px;
    }

  }

  input,
  textarea {
    outline: none;
    border: none;
    font-family: inherit;
    font-size: inherit;
    font-weight: 300;
    color: rgba(255, 255, 255, .9);
    padding: 16px 0;
    width: 100%;
    background: transparent;

    &[type="number"]::-webkit-inner-spin-button {
      display: none;
      appearance: none;
    }

    input[type=number] {
      appearance: none;
      -moz-appearance: textfield;
    }

    &:focus {
      ~label {
        top: 0;
        font-size: 13px;

        ~span {
          width: 100%;
          opacity: 1;
        }
      }
    }

    textarea {
      resize: none;
      height: 90px;

      &::-webkit-scrollbar-thumb {
        border-radius: 7px;
      }
    }

    &.hobby {
      display: none;
    }
  }

}

footer {
  background: rgba(0, 0, 0, 0.3);
  font-size: 14px;
  padding: 14px;
  text-align: center;
  color: rgba(250, 250, 250, 0.7);
}